
  import { mapGetters, mapActions } from 'vuex'
  import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

  import None from '../../components/None'
  import TabelView from '../../components/TabelView'
  export default {
    components: {
      Scroller,
      Spinner,
      None,
      TabelView,
      Divider
    },
    data () {
      return {
        pageid: 1,
        //shifts: [],
        status: {
          pulldownStatus: 'default',
          pullupStatus: 'default'
        },
        downConf: { // 下拉配置
          content: 'Pull Down To Refresh',
          height: 60,
          autoRefresh: false,
          downContent: '下拉刷新',
          upContent: '加载中...',
          loadingContent: '加载中...',
          clsPrefix: 'xs-plugin-pulldown-'
        },
	    upConf: {
		  content: 'Pull Up To Refresh',
		  pullUpHeight: 60,
		  height: 40,
		  autoRefresh: false,
		  downContent: '上拉加载',
		  upContent: '加载中...',
		  loadingContent: '加载中...',
		  clsPrefix: 'xs-plugin-pullup-'
		}
      }
    },
    computed: {
      ...mapGetters({
        shifts: 'todayShifts'
      })
    },
    methods: {
      ...mapActions([
        'getTodayShifts'
      ])
    },
    watch: { // 监听下拉下拉的状态
      status (e) {
        const $this = this
        if (e.pulldownStatus === 'loading') {  //下拉刷新
          setTimeout(() => {
            $this.getTodayShifts().then((res) => {
              $this.$nextTick(() => {
		        this.$refs.scroller.reset()
		      })
              $this.status.pulldownStatus = 'default'
            })
          }, 500)
        } else if (e.pullupStatus === 'loading') { //上拉加载
          setTimeout(() => {
            $this.getTodayShifts().then((res) => {
			      if(res.shiftInfos.length < 15) {
			      	$this.status.pullupStatus = 'disabled'
			      }else {
			      	$this.status.pullupStatus = 'default'
			      }
                $this.$nextTick(() => {
		           this.$refs.scroller.reset()
		        })
            })
          }, 1000)
        }
      }
    },
    created () {
      if(this.shifts.length <= 0) {
      	  this.$store.state.comm.loadMore = 'loading'
	      this.$store.state.shifts.todayShifts.pageid = 1
	      this.getTodayShifts().then((res) => {
	      	this.$store.state.comm.loadMore = 'success'
		      if(res.shiftInfos.length < 15) {
		      	this.status.pullupStatus = 'disabled'
		      }
		      this.$nextTick(() => {
		        this.$refs.scroller.reset()
		      })
		  })
	   }
    }
  }



